import React, { useState, useEffect } from "react";
import meter1 from "../assets/img/meter1.svg";
import meter2 from "../assets/img/meter2.svg";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import colorSharp from "../assets/img/color-sharp.png";
import axios from "axios";

export const Skills = () => {
  const [price, setPrice] = useState(null);  // Estado para el precio actual
  const [error, setError] = useState(null);  // Estado para manejar errores

  // Función para obtener el precio desde el servidor
  const fetchPrice = async () => {
    try {
      const response = await axios.get("http://localhost:5000/api/getprice");
      setPrice(response.data.price); // Guardar el precio en el estado
    } catch (err) {
      console.error("Error al obtener el precio:", err);
      setError("No se pudo obtener el precio");
    }
  };

  // Llamada a la función cuando el componente se monta
  useEffect(() => {
    fetchPrice(); // Llamada a la función para obtener el precio
  }, []);

  const responsive = {
    superLargeDesktop: {
      breakpoint: { max: 4000, min: 3000 },
      items: 2,
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 2,
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2,
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
    },
  };

  return (
    <section className="skill" id="skills">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className="skill-bx wow zoomIn">
              <h2>Distribution per block</h2>
              <p>
                The Developer Fee will be used only for project development purposes
                such as exchange listings and providing liquidity
              </p>
              <Carousel
                responsive={responsive}
                infinite={true}
                arrows={false}
                className="owl-carousel owl-theme skill-slider"
              >
                <div className="item">
                  <img src={meter1} alt="Mining" />
                  <h5>Mining</h5>
                  <p className="small-text">4750 Akic</p>
                </div>
                <div className="item">
                  <img src={meter2} alt="Dev Fee" />
                  <h5>Dev Fee</h5>
                  <p className="small-text">250 Akic</p>
                </div>
              </Carousel>
              {/* Mostrar precio actual aquí */}
              <div style={{ marginTop: "20px", textAlign: "center" }}>
                {error ? (
                  <p style={{ color: "red" }}>{error}</p>
                ) : price ? (
                  <p>
                    <strong>Actual Price:</strong> {price}
                  </p>
                ) : (
                  <p>Loading price...</p>
                )}
              </div>
            </div>
          </div>
        </div>
      </div>
      <img className="background-image-left" src={colorSharp} alt="Background" />
    </section>
  );
};
