import { Container, Row, Col, Tab, Nav } from "react-bootstrap";
import { ProjectCard } from "./ProjectCard";
import projImg1 from "../assets/img/project-img1.png";
import projImg2 from "../assets/img/project-img2.png";
import projImg3 from "../assets/img/project-img3.png";
import projImg4 from "../assets/img/project-img4.png";
import projImg5 from "../assets/img/project-img5.png";
import projImg6 from "../assets/img/project-img6.png";
import colorSharp2 from "../assets/img/color-sharp2.png";
import 'animate.css';
import TrackVisibility from 'react-on-screen';
import ReactPlayer from "react-player";
import React, { useEffect, useState } from 'react';
import axios from 'axios';

export const Projects = () => {
  const [difficulty, setDifficulty] = useState('');
  const [blockcount, setBlockcount] = useState('');
  const [networkHashrate, setNetworkHashrate] = useState('');
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(false);

  // Solicitudes HTTP a las rutas de tu backend
  useEffect(() => {
    const fetchData = async () => {
      try {
        // Fetch dificultad
        const difficultyResponse = await axios.get('http://localhost:5000/api/getdifficulty');
        setDifficulty(difficultyResponse.data.difficulty);

        // Fetch block count
        const blockcountResponse = await axios.get('http://localhost:5000/api/getblockcount');
        setBlockcount(blockcountResponse.data.totalBlocks);

        // Fetch network hashrate
        const networkHashrateResponse = await axios.get('http://localhost:5000/api/getnetworkhashps');
        setNetworkHashrate(networkHashrateResponse.data.networkHashrate);

        setLoading(false);
      } catch (error) {
        console.error("Error fetching data:", error);
        setError(true);
        setLoading(false);
      }
    };

    fetchData();
  }, []);

  // Proyectos con la descripción dinámica basada en la API
  const projects = [
    {
      title: "Current Difficulty",
      description: loading ? "Cargando..." : error ? "Error al cargar" : difficulty, // Descripción dinámica
      imgUrl: projImg1,
    },
    {
      title: "Current Block Count",
      description: loading ? "Cargando..." : error ? "Error al cargar" : blockcount, // Descripción dinámica
      imgUrl: projImg2,
    },
    {
      title: "Network Hashrate",
      description: loading ? "Cargando..." : error ? "Error al cargar" : networkHashrate, // Descripción dinámica
      imgUrl: projImg3,
    },
    {
      title: "JavaScript",
      description: "Descripción de prueba para JavaScript",
      imgUrl: projImg4,
    },
    {
      title: "Django",
      description: "Descripción de prueba para Django",
      imgUrl: projImg5,
    },
    {
      title: "Laravel",
      description: "Descripción de prueba para Laravel",
      imgUrl: projImg6,
    },
  ];

  return (
    <section className="project" id="projects">
      <Container>
        <Row>
          <Col size={12}>
            <TrackVisibility>
              {({ isVisible }) =>
                <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                  <h2>Network Information</h2>
                  <p>Información de la red actual</p>
                  <Tab.Container id="projects-tabs" defaultActiveKey="first">
                    <Nav variant="pills" className="nav-pills mb-5 justify-content-center align-items-center" id="pills-tab">
                      <Nav.Item>
                        <Nav.Link eventKey="first">API Calls</Nav.Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Nav.Link eventKey="second">Video Sample</Nav.Link>
                      </Nav.Item>
                      <Nav.Item>
                        <Nav.Link eventKey="third">Community</Nav.Link>
                      </Nav.Item>
                    </Nav>
                    <Tab.Content id="slideInUp" className={isVisible ? "animate__animated animate__slideInUp" : ""}>
                      <Tab.Pane eventKey="first">
                        <Row>
                          {projects.map((project, index) => (
                            <ProjectCard key={index} {...project} />
                          ))}
                        </Row>
                      </Tab.Pane>
                      <Tab.Pane eventKey="second">
                        <p>A short video that shows a video game slightly modifying the RPGMaker MV system to resemble the video game Undertale; the designs shown are from the mentioned video game.</p>
                        <p>The game is made in Spanish (my native language)</p>
                        <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                        </div>
                      </Tab.Pane>
                      <Tab.Pane eventKey="third">
                        <p>I started programming simple things around middle school, and in high school, I continued programming, including simple conditionals, as I had a certain liking for programming. I have some color palettes that I commonly use for web development, and I have also ventured into game development as a simple hobby.</p>
                      </Tab.Pane>
                    </Tab.Content>
                  </Tab.Container>
                </div>}
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
      <img className="background-image-right" src={colorSharp2} alt="background" />
    </section>
  );
};
